import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

function SuggestRemoteRepo (props) {
  const { onDismiss, missingPath } = props
  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Missing keyboard definition?</h2>
        <p>
          Hi there! The repository you're trying to load from doesn't have a
          defined <code>{missingPath}</code>. Right now this application is
          expecting to look at that directory to discover keyboard definitions
          for experimental features like automatic layout generation.
        </p>
        <p>
          Is it possible that the keyboard you're trying to edit relies on data
          from the ZMK or other repositories? Fetching from other repositories is
          not currently supported.
        </p>
        <p>
          I'm a humble developer and have only worked with configurations for my
          own handwired builds, so if you feel you're receiving this message in
          error then please consider opening an issue on this project's GitHub
          repository to let me know what I can do differently.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestRemoteRepo.propTypes = {
  onDismiss: PropTypes.func.isRequired,
  missingPath: PropTypes.string.isRequired
}

export default SuggestRemoteRepo
