import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GitHubIssuesLink } from './support'

function MissingKeymaps (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>No Keymap Available</h2>
        <p>
          We couldn't find a <code>.keymap</code> file in your <code>config/</code> folder.
        </p>
        <p>
          Please ensure that any keymap(s) you wish to edit are placed in that
          directory even if an accompanying keyboard definition exists in your
          under the <code>config/boards/...</code> directory.
        </p>
        <p>
          If you have any questions please feel free to reach out by creating an
          issue at {GitHubIssuesLink}.
        </p>
      </DialogBox>
    </Modal>
  )
}

MissingKeymaps.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default MissingKeymaps
