import { createSlice } from '@reduxjs/toolkit'
import find from 'lodash/find'
import map from 'lodash/map'
import { useMemo, useReducer } from 'react'

import github from './api'
import * as storage from './storage'

const INITIAL_STATE = {
  blocked: false,
  initialized: false,
  selectedRepoId: null,
  selectedBranchName: null,
  selectedKeyboard: null,
  branches: [],
  keyboards: [],
  loading: false,
  warningFlags: []
}

export const pickerSlice = createSlice({
  name: 'github-picker',
  initialState: INITIAL_STATE,
  reducers: {
    clearSelection (state) {
      state.warningFlags = []
      state.loading = false
    },
    integrationInitialized (state) {
      const persistedRepoId = storage.getPersistedRepository()
      const repositories = github.repositories || []
      let selectedRepoId

      if (find(repositories, { id: persistedRepoId })) {
        selectedRepoId = persistedRepoId
      } else if (repositories.length > 0) {
        selectedRepoId = repositories[0].id
      }

      state.initialized = true
      state.selectedRepoId = selectedRepoId
    },
    loadingBranchesList (state) { state.loading = true },
    loadedBranchesList (state, action) {
      const { selectedRepoId } = state
      const { branches } = action.payload
      const repository = find(github.repositories, { id: selectedRepoId })

      state.branches = branches
      state.keyboard = null
      state.keyboards = []
      state.loading = false

      const available = map(branches, 'name')
      const defaultBranch = repository.default_branch
      const previousBranch = storage.getPersistedBranch(selectedRepoId)
      const onlyBranch = branches.length === 1 ? branches[0].name : null

      for (const branch of [onlyBranch, previousBranch, defaultBranch]) {
        if (available.includes(branch)) {
          state.selectedBranchName = branch
          state.selectedKeyboard = storage.getPersistedKeyboard(selectedRepoId, branch)
          break
        }
      }
    },
    loadingKeyboardsList (state) { state.loading = true },
    loadedKeyboardsList (state, action) {
      const { selectedRepoId, selectedBranchName } = state
      const { keyboards, default: defaultKeyboard } = action.payload

      state.keyboards = keyboards
      state.loading = false

      const previousKeyboard = storage.getPersistedKeyboard(selectedRepoId, selectedBranchName)
      const onlyKeyboard = keyboards.length === 1 ? keyboards[0] : null

      for (const keyboard of [onlyKeyboard, previousKeyboard, defaultKeyboard]) {
        if (keyboards.includes(keyboard)) {
          state.selectedKeyboard = keyboard
          break
        }
      }
    },
    selectedRepository (state, action) {
      state.selectedRepoId = action.payload.repository.id
      state.selectedBranchName = null
      state.selectedKeyboard = null

      storage.setPersistedRepository(state.selectedRepoId)
    },
    selectedBranch (state, action) {
      state.selectedBranchName = action.payload.name
      state.selectedKeyboard = storage.getPersistedKeyboard(
        state.selectedRepoId,
        state.selectedBranchName
      )
      storage.setPersistedBranch(
        state.selectedRepoId,
        state.selectedBranchName
      )
    },
    selectedKeyboard (state, action) {
      state.selectedKeyboard = action.payload.name
      storage.setPersistedKeyboard(
        state.selectedRepoId,
        state.selectedBranchName,
        state.selectedKeyboard
      )
    },
    keyboardLoading (state, action) {
      state.loading = true
    },
    keyboardLoadedSuccess (state, action) {
      const { response: { keymap } } = action.payload
      state.warningFlags = keymap.warningFlags || []
      state.loading = false
    }
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(pickerSlice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(pickerSlice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function usePickerStore () {
  const [state, dispatch] = useReducer(pickerSlice.reducer, INITIAL_STATE)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}
