import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'
import IconButton from '../../../Common/IconButton'
import github from '../api'

function Install () {
  const greeting = github.username
    ? `Welcome, ${github.username}!`
    : 'Welcome!'

  return (
    <Modal centerOnly>
      <DialogBox dismissText={null}>
        <h2>{greeting}</h2>
        <p>
          To begin import your own keymaps, you'll need to grant the Keymap
          Editor app access to the relevant GitHub repositories. Please make
          sure to only select repositories that are set up for ZMK keyboard
          configurations.
        </p>
        <p>
          This app requires read and write access to the repositories you select
          in order to write to arbitrary <code>.keymap</code> files. It also
          receives all build action events for the repository, which generates
          quite a bit of traffic. Selecting <code>All repositories</code> for
          the app installation will lead to a suspension.
        </p>
        <IconButton
          collection="brands"
          icon="github"
          text="Add Repository"
          style={{ display: 'block', margin: '0 auto' }}
          onClick={() => github.beginInstallAppFlow()}
        />
      </DialogBox>
    </Modal>
  )
}

Install.propTypes = {}

export default Install
