import find from 'lodash/find'

import { Control, Select } from '../Controls'
import Spinner from '../../Common/Spinner'

import github from './api'
import Icon from '../../Common/Icon'

function Protected () {
  return (
    <Icon
      name="shield"
      title="Branch is protected -- cannot commit changes"
    />
  )
}

export function GithHubExpandedPicker ({ state, actions, choices, selections }) {
  const { repositories, repositoryChoices, branchChoices, keyboardChoices } = choices
  const { selectedRepoChoice, selectedBranchChoice, selectedKeyboardChoice } = selections
  const { branches, keyboards, loading } = state

  function handleSelectRepo ({ id, type }) {
    if (type === 'installation') {
      github.manageInstallationRepos(id)
      return
    }

    const repository = find(repositories, { id })
    actions.selectedRepository({ repository })
  }

  return (
    <>
      <Select
        label="Repository"
        value={selectedRepoChoice}
        options={repositoryChoices}
        onChange={handleSelectRepo}
        getValue={option => option.id}
        getOptionValue={option => option.id}
        getOptionLabel={option => option.full || option.name}
        formatOptionLabel={(option, { context }) => (
          <>{context === 'value' ? option.name : option.full || option.name}</>
        )}
      />

      {branches.length > 1 && (
        <Select
          label="Branch"
          value={selectedBranchChoice}
          options={branchChoices}
          onChange={({ id }) => actions.selectedBranch({ name: id })}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
          formatOptionLabel={option => (
            <>
              {option.protected && <Protected />} {option.name}
            </>
          )}
        />
      )}

      {keyboards.length > 1 && (
        <Select
          label="Keyboard"
          value={selectedKeyboardChoice}
          options={keyboardChoices}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
          onChange={({ id }) => actions.selectedKeyboard({ name: id })}
        />
      )}

      {loading && (
        <Control>
          <Spinner />
        </Control>
      )}
    </>
  )
}
