import find from 'lodash/find'
import { useEffect, useMemo } from 'react'

import github from './api'

// TODO: most of this should probably just be turned into thunks in the reducer

export function useRepositoryOptions (state, actions) {
  const {
    branches,
    keyboards,
    selectedRepoId,
    selectedBranchName,
    selectedKeyboard
  } = state

  const repositories = (github.repositories || []).map(repo => ({
    id: repo.id,
    name: repo.name,
    full: repo.full_name
  }))

  const extras = (github.installations || []).map(installation => ({
    id: installation.id,
    type: 'installation',
    name: `[Manage available repos for ${installation.account.login}...]`
  }))

  const repositoryChoices = useMemo(() => [...repositories, ...extras], [repositories, extras])
  const branchChoices = useMemo(() => branches.map(branch => ({ id: branch.name, name: branch.name, protected: branch.protected })), [branches])
  const keyboardChoices = useMemo(() => keyboards.map(keyboard => ({ id: keyboard, name: keyboard })), [keyboards])

  const selectedRepoChoice = useMemo(() => find(repositories, { id: selectedRepoId }), [repositories, selectedRepoId])
  const selectedBranchChoice = useMemo(() => find(branchChoices, { id: selectedBranchName }), [branchChoices, selectedBranchName])
  const selectedKeyboardChoice = useMemo(() => find(keyboardChoices, { id: selectedKeyboard }), [keyboardChoices, selectedKeyboard])

  useEffect(() => {
    if (!selectedRepoId) {
      return
    }

    (async function () {
      actions.loadingBranchesList()

      const repository = find(github.repositories, { id: selectedRepoId })
      const branches = await github.fetchRepoBranches(repository)

      actions.loadedBranchesList({ branches })
    })()
  }, [selectedRepoId, actions])

  useEffect(() => {
    if (!selectedRepoId || !selectedBranchName) {
      return
    }

    (async function () {
      actions.loadingKeyboardsList()

      const repository = find(github.repositories, { id: selectedRepoId })
      const keyboardsList = await github.fetchBranchKeyboards(repository, selectedBranchName)

      actions.loadedKeyboardsList(keyboardsList)
    })()
  }, [selectedRepoId, selectedBranchName, actions])

  return [
    { repositories, repositoryChoices, branchChoices, keyboardChoices },
    { selectedRepoChoice, selectedBranchChoice, selectedKeyboardChoice }
  ]
}
