import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GeneralHelpOfferCopy, KeymapLayoutResources } from './support'

function MissingLayout (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>No Layout Available</h2>
        <p>
          Your keymap doesn't have an accompanying layout and we don't know
          enough about your keyboard to generate one for you automatically.
        </p>
        <p>
          If you meant to include layout data, please make sure that its in the
          same directory as your keymap,
          like <code>config/info.json</code> or <code>config/&lt;keyboard&gt;.json</code>.
        </p>
        {KeymapLayoutResources}
        {GeneralHelpOfferCopy}
      </DialogBox>
    </Modal>
  )
}

MissingLayout.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default MissingLayout
