import find from 'lodash/find'
import isMatch from 'lodash/isMatch'
import { useCallback, useEffect } from 'react'
import PropTypes from 'prop-types'

import github from './api'
import ValidationErrors from './ValidationErrors'
import { usePickerStore } from './store'

import { Button } from '../Controls'
import SuggestRemoteRepo from '../../Dialogs/SuggestRemoteRepo'
import SuggestRepoSelection from '../../Dialogs/SuggestRepoSelection'
import SuggestWrongRepo from '../../Dialogs/SuggestWrongRepo'
import LayoutGenerationError from '../../Dialogs/LayoutGenerationError'
import Blocked from '../../Dialogs/Blocked'
import MissingLayout from '../../Dialogs/MissingLayout'

import Login from './Dialogs/Login'
import Install from './Dialogs/Install'
import Icon from '../../Common/Icon'
import MissingKeymaps from '../../Dialogs/MissingKeymaps'
import { GithHubExpandedPicker } from './GitHubExpandedPicker'
// import { GitHubCompactPicker } from './GitHubCompactPicker'

import { useRepositoryOptions } from './repository-options'
// import { Actions } from './Actions'

const isMissingConfig = err => isMatch(err, { name: 'MissingRepoFile', path: 'config' })
const isMissingKeymaps = err => isMatch(err, { name: 'MissingRepoFile', intent: 'directory-structure' })
const isMissingLayout = err => (
  isMatch(err, { name: 'MissingRepoFile', path: 'config/info.json' }) ||
  isMatch(err, { name: 'MissingRepoFile', intent: 'metadata' })
)
const isMissingBoard = err => (
  err?.name === 'MissingRepoFile' &&
  err?.path.match(/config\/boards\/[^/]+\/[^/]+\/[^/]+?\..*$/)
)

function GithubPicker (props) {
  const [state, actions] = usePickerStore()
  const [choices, selections] = useRepositoryOptions(state, actions)

  const { initialized, selectedRepoId, selectedBranchName } = state
  const { selectedKeyboard } = state
  const { loading } = state

  const { expanded, onSelect, onExpand, onError } = props

  useEffect(() => {
    const login = () => github.beginLoginFlow()
    const blocked = () => { actions.clearSelection(); onError({ dialog: Blocked }) }
    const layoutGenerationError = () => { actions.clearSelection(); onError({ dialog: LayoutGenerationError }) }
    const repoValidation = error => {
      let dialog = ValidationErrors
      if (isMissingConfig(error)) dialog = SuggestWrongRepo
      if (isMissingBoard(error)) dialog = SuggestRemoteRepo
      if (isMissingLayout(error)) dialog = MissingLayout
      if (isMissingKeymaps(error)) dialog = MissingKeymaps

      actions.clearSelection()
      onError({
        dialog,
        dialogProps: {
          title: error.name,
          errors: error.errors
        }
      })
    }

    github.init().then(actions.integrationInitialized)
    github.on('authentication-failed', login)
    github.on('blocked', blocked)
    github.on('repo-validation-error', repoValidation)
    github.on('layout-generation-failed', layoutGenerationError)

    return () => {
      github.off('authentication-failed', login)
      github.off('blocked', blocked)
      github.off('repo-validation-error', repoValidation)
      github.off('layout-generation-failed', layoutGenerationError)
    }
  }, [actions, onError])

  const loadKeyboard = useCallback(async function (repoId, branch, keyboard) {
    const available = github.repositories
    const repository = find(available, { id: repoId })?.full_name
    const isProtected = find(choices.branchChoices, { id: branch })?.protected

    actions.keyboardLoading()

    const response = await github.fetchLayoutAndKeymap(repository, branch, {
      keyboard
    })

    actions.keyboardLoadedSuccess({ response })

    onSelect({ github: { repository, branch, keyboard, isProtected }, ...response })
  }, [actions, choices.branchChoices, onSelect])

  const reloadKeyboard = useCallback(() => (
    loadKeyboard(selectedRepoId, selectedBranchName, selectedKeyboard)
  ), [loadKeyboard, selectedRepoId, selectedBranchName, selectedKeyboard])

  useEffect(() => {
    if (!selectedRepoId || !selectedBranchName) {
      return
    }

    loadKeyboard(selectedRepoId, selectedBranchName, selectedKeyboard)
  }, [selectedRepoId, selectedBranchName, selectedKeyboard, loadKeyboard])

  if (!initialized) return null
  if (state.blocked) return <Blocked />
  if (!github.isGitHubAuthorized()) return <Login />
  if (!github.isAppInstalled()) return <Install />
  if (github.isAppSuspendedForRepoSelection()) {
    return <SuggestRepoSelection installation={github.installations[0]} />
  }

  return (
    <>
      <GithHubExpandedPicker state={state} actions={actions} choices={choices} selections={selections} />
      {/* {expanded
        ? <GithHubExpandedPicker state={state} actions={actions} choices={choices} selections={selections} />
        : <GitHubCompactPicker onExpand={onExpand} state={state} actions={actions} choices={choices} selections={selections} />} */}
      {selectedBranchName && !loading && (
        <>
          <Button onClick={reloadKeyboard}>
            <Icon name="sync" />
          </Button>
          {/* <Actions /> */}
        </>
      )}
    </>
  )
}

GithubPicker.propTypes = {
  expanded: PropTypes.bool.isRequired,
  onExpand: PropTypes.func.isRequired,
  onError: PropTypes.func.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default GithubPicker
