import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { GeneralHelpOfferCopy } from './support'

function SuggestWrongRepo (props) {
  const { onDismiss } = props

  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Is this a zmk-config repo?</h2>
        <p>
          Hi there! The repository you're trying to load from doesn't have a
          defined <code>config</code> directory. Right now this application is
          expecting to look at that directory to discover keymaps and other
          metadata.
        </p>
        {GeneralHelpOfferCopy}
      </DialogBox>
    </Modal>
  )
}

SuggestWrongRepo.propTypes = {
  onDismiss: PropTypes.func.isRequired,
  otherRepoOrBranchAvailable: PropTypes.bool
}

export default SuggestWrongRepo
